@import "https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Baloo Paaji 2", cursive;
}

body {
  background: linear-gradient(to bottom right, #224ead 0%, #c01821 50%, #224ead 100%);
}

.container {
  max-width: 992px;
  min-height: 100vh;
  flex-direction: column;
  margin: auto;
  display: flex;
}

.header {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  display: flex;
}

span {
  color: #eb196e;
  font-size: 2.5rem;
}

.title-page {
  text-align: center;
  color: #fff;
  margin-top: 30px;
}

.card-container {
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  margin: 30px 0;
  display: grid;
}

.button-wrap {
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  display: flex;
}

.card {
  height: 100%;
  width: 100%;
  box-shadow: #000000b3;
  color: #fff;
  background-color: #222831;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  display: flex;
}

.active {
  background-color: #224ead;
}

.image {
  height: 160px;
  width: 160px;
  border: 5px solid #272133;
  border-radius: 50%;
  margin-top: 20px;
  box-shadow: 0 10px 50px gray;
}

.choose-btn {
  color: #fff;
  cursor: pointer;
  min-width: 150px;
  background-color: #eb196e;
  border: none;
  border-radius: 3px;
  margin: 0 5px;
  padding: 8px 16px;
  font-weight: bolder;
  transition: all .2s ease-in;
}

.choose-btn:last-child {
  margin: 2px 0 2px 2px;
}

.choose-btn:hover {
  background-color: #224ead;
}

.disable-btn {
  color: #929292;
  background-color: #585858;
  border: none;
  border-radius: 3px;
  margin: 0 5px;
  padding: 8px 16px;
  font-weight: bolder;
}

#visible-btn {
  max-width: 95%;
  margin-top: -30px;
  margin-bottom: 20px;
  padding: 8px 16px;
}

.primaryBtn {
  cursor: pointer;
  color: #fff;
  background: #185adb;
  border: none;
  border-radius: 15px;
  margin: 20px 10px;
  padding: 13px 25px;
  font-size: .8rem;
  font-weight: 500;
  transition: all .25s;
}

.primaryBtn:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px -10px #185adb99;
}

.container-loading {
  width: 100%;
  margin: auto;
}

.loading {
  color: #fff;
  text-align: center;
}

.card-wraper {
  width: 100%;
}

.city-image {
  width: 100%;
  height: 10rem;
  margin-bottom: 10px;
  overflow: hidden;
}

.city-image img {
  width: 100%;
}

.button-title-city {
  justify-content: center;
  display: flex;
}

.darkBG {
  width: 100vw;
  height: 100vh;
  z-index: 0;
  background-color: #0003;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal {
  min-width: 30rem;
  min-height: 30rem;
  color: #fff;
  z-index: 10;
  border-radius: 3px;
  box-shadow: 0 5px 20px #0000000a;
}

.modalHeader {
  height: 50px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  overflow: hidden;
}

.heading {
  color: #fff;
  text-align: center;
  margin: 0;
  padding: 10px;
  font-size: 18px;
  font-weight: 500;
}

.loadingModal {
  margin-top: 35%;
}

.modalContent {
  color: #2c3e50;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.villainAvatar {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}

.villainAvatar img {
  width: 100%;
  height: 100%;
  border: 3px solid #eb196e;
  border-radius: 50%;
  box-shadow: 0 -5px 30px #333;
}

.boxStatus {
  height: 50px;
  width: 80%;
  border: 2px solid #2c3e50;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  display: flex;
}

.progress {
  min-width: 200px;
  max-width: 200px;
  height: 30px;
  border: 2px solid #2c3e50;
  margin: 20px auto;
  padding: 2px 1px;
  font-weight: bold;
  overflow: hidden;
}

.progressBar {
  height: 100%;
  background: repeating-linear-gradient(135deg, #fa68a2, #fa68a2 20px, #19baeb 20px 40px);
  transition: all 1s;
  animation: fill-bar 1s;
}

@keyframes fill-bar {
  from {
    width: 0%;
  }

  to {
    width: 100%;
  }
}

.statusHP {
  position: absolute;
  left: 48%;
}

.modalActions {
  width: 100%;
  margin-bottom: 10px;
  bottom: 2px;
}

.actionsContainer {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.closeBtn {
  cursor: pointer;
  color: #2c3e50;
  background: #fff;
  border: none;
  border-radius: 3px;
  align-self: flex-end;
  margin-top: -7px;
  margin-right: -7px;
  padding: 4px 8px;
  font-size: 18px;
  font-weight: 500;
  transition: all .25s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 5px 20px #0000000f;
}

.closeBtn:hover {
  color: #fff;
  background: #eb196e;
  transform: translate(-7px, 7px);
