  padding: 8px 13px;
  font-size: .9rem;
  font-weight: 500;
  transition: all .25s;
}

.fightBtn:hover {
  background: #ee2d7a;
  box-shadow: 0 10px 20px -10px #ff3e4e99;
}

.disableBtn {
  min-width: 120px;
  cursor: pointer;
  color: #ee2d7a;
  border: none;
  border-radius: 3px;
  margin-top: 10px;
  padding: 8px 13px;
  font-size: .9rem;
  font-weight: 500;
